const Header = props => {
  const { mode } = props;
  return !mode ? (
    <p>Выберите песню</p>
  ) : (
    <p onClick={props.onHeaderClick}>Назад</p>
  );
};

const Main = props => {
  const renderTemplate = props => {
    let Template = null;
    const { data } = props;

    Template = data.map((item, i) => {
      return !i ? null : (
        <button key={i} onClick={() => props.onBtnClick(i)} className="btn">
          {i}
        </button>
      );
    });
    return Template;
  };
  const { mode } = props;
  return !mode ? (
    <div className="App-main">{renderTemplate(props)}</div>
  ) : (
    <pre>{songs[mode]}</pre>
  );
};

class App extends React.Component {
  state = {
    display: 0
  };

  handleButtonClick = e => {
    this.setState({ display: e });
  };
  handleHeaderClick = () => {
    this.setState({ display: 0 });
  };
  render() {
    return (
      <div className="App">
        <Header
          mode={this.state.display}
          onHeaderClick={this.handleHeaderClick}
        />
        <Main
          mode={this.state.display}
          data={songs}
          onBtnClick={this.handleButtonClick}
        />
      </div>
    );
  }
}

export default App;
